<script>

import other from './Pages/profile/modules/modal/other'
import { Slide } from 'vue-burger-menu'
export default {
  name: 'headerNav',
  components: { other, Slide },
  data: function () {
    return {
      count: 0,
      val: -40,
      max: 40,
      isActive: true,
      focused: false,
      isGreen: false,
      isRed: false,
      isDown: false,
      isUp: false,
      showModal: false
    }
  },
  methods: {
    // animation focus input header
    onFocus () {
      this.focused = true
      this.isActive = false
    },
    onBlur () {
      this.focused = false
      this.isActive = true
    },
    /* function counter and scroll statistics header  */
    onScroll (e) {
      if (this.$el.offsetTop + this.$el.offsetHeight < window.scrollY + window.innerHeight) {
        this.removeScrollHandler()
        this.test = true
        const interval = setInterval(() => {
          if (this.val >= this.count) {
            this.isGreen = true
            this.isUp = true
          } else {
            this.isRed = true
            this.isDown = true
          }
          if (++this.val === this.max) {
            clearInterval(interval)
          }
        }, 4000 / this.max)
      }
    },
    /* end function counter and scroll statistics header */
    removeScrollHandler () {
      window.removeEventListener('scroll', this.onScroll)
    }
  },
  computed: {
    currentTabComponent: function () {
      return this.currentTab.toLowerCase()
    }
  },
  mounted () {
    window.addEventListener('scroll', this.onScroll)
    this.onScroll()
  },
  beforeDestroy () {
    this.removeScrollHandler()
  }
}

</script>

<style lang="scss" scoped>
  /* local styles witch header component */

  /* style count header animation */
  .minus{
    color: red;
  }
  .plus{
    color: #02B033;
  }
  .down{
    transform: rotate(90deg);
   path {
     stroke: red;
   }
  }
  .up{
    transform: none;
    path {
      stroke: #02B033;
    }
  }
  .rating-icon__up{
    transform: rotate(0deg);
  }

  /* style input animation */
  .active{
    outline: 1px solid #0275B1;
  }
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-enter-opacity-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-opacity-leave-active {
    transition: all 1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active   2.1.8 */ {
    transform: translateX(1px);
    opacity: 0;
  }.slide-fade-opacity-enter, .slide-fade-opacity-leave-to
     /* .slide-fade-leave-active   2.1.8 */ {
     transform: translateX(30px);
     opacity: 0;
   }
 /* media header logo */
 @media (max-width: 576px) {
   .header__logo{
     display: none;
   }
 }
</style>
